.container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

#quote-box {
  border-radius: 3px;
  position: relative;
  width: 450px;
  padding: 40px 50px;
  display: table;
  background-color: #fff;
}

#text {
  font-size: 1.75em;
  font-weight: 500;
  clear: both;
}

#author {
  text-align: right;
  font-size: 1em;
  margin-top: 30px;
}

#quote-box .buttons {
  width: 450px;
  display: inline-block;
}

#quote-box .buttons .button#tumblr-quote,
#quote-box .buttons .button#tweet-quote {
  border: none;
  border-radius: 3px;
  color: #fff;
  outline: none;
  margin-top: 30px;
  opacity: 1;
  cursor: pointer;
  text-align: center;
  height: 38px;
}

#quote-box .buttons a {
  float: left;
  padding-top: 8px;
  font-size: 1.2em;
  margin-right: 5px;
  height: 30px;
  width: 40px;
}

#new-quote {
  float: right;
  height: 46px;
  border: none;
  border-radius: 3px;
  color: #fff;
  outline: none;
  font-size: 1em;
  font-weight: 600;
  padding: 8px 18px 6px 18px;
  margin-top: 30px;
  opacity: 1;
}
